
.item__menu.cart{
    margin-top: 1rem;
}

.container__cart{
    width: 50px;
    position: relative;
}

.container__cart:hover{
    transform: scale(1.2);
}

.cart{
    cursor: pointer;
    color: var(--primary);
}

.bi.bi-cart{
    font-size: 2rem;
    color: var(--primary);
}

.bi.bi-cart.active{
    font-size: 2rem;
    color: var(--secondary);
}

.container__quant{
    position: absolute;
    top: 0;
    right: 0;
}

.quantities{
    font-size: 1.5rem;
    padding: 4px 7px;
    border-radius: 50%;
    background-color:hsla(18, 85%, 52%, 0.66);

}
