
.container__slider{
    display: flex;
    width: 100%;
}

.slider{
    width: 100%;
    max-width: 900px;
    margin-left: 0;
    overflow: hidden;
    border: 3px solid var(--tertiary);
    position: relative;
}

.slider__{
    display: flex;
    width: 400%;
    height: 400px;
}

.slider__section{
    width: 100%;
}

.slider__img{
    display: block;
    width: 100%;
    height: 100%;
}

.slider__btn{
    position: absolute;
    top: 40%;
    font-size: 40px;
    cursor: pointer;
    color: var(--hover-img);
}

.slider__btn:hover{
    color: var(--secondary);
}

.slider__btn__right{
    right: 0;
}