nav{
    display: flex;
    flex-direction: column;
  }
  
  .container__nav{
    background-color: var(--secondary);
    font-family: 'Exo 2', sans-serif;
  }
  
  aside{
    padding: 2rem;
    padding-right: 0;
    position: sticky;
    top: 0;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: center;
  }
  
  .menu{
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
  }
  
  .item__menu{
    display: flex;
    flex-direction: row;
    padding: 0.7rem;
    cursor: pointer;
  }
  
  .button__menu{
    background-color: transparent;
    border: 0;
    cursor: pointer;
    color: var(--primary);
    font-size: 1.1rem;
    font-family: 'Exo 2', sans-serif;
    font-weight: 500;
  }
  
  .button__menu:hover{
    text-decoration: underline;
  }
  
  .button__menu.active{
    color: var(--secondary);
  }
  
  .item__menu.active{
    background-color: var(--primary);
    width: 100%;
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
  }
  
  .button__menu.active:hover{
    text-decoration: none;
  }
  
  